footer {
  color: aliceblue;
  width: 100%;
  background-color: rgb(39, 39, 39);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;


}

.footer-li:hover {
  cursor: pointer;
  color: #007bff;
  text-decoration: underline;
}

.footer-li:hover {
  text-decoration: underline;

}

a {
  color: inherit;
  text-decoration: none;
}


a:hover {
  color: #007bff;
  text-decoration: underline;
}

ul {
  margin: 0;
}

.footer-right {
  padding-top: 20px;
}

.footer-middle{
  display: flex;
  flex-direction: column;
  align-items: baseline;
}
.footer-middle p{
  margin-bottom:0;
  padding-left: 25px;
}


/************ Responsive smartphone***********/
@media screen and (max-width: 767px) {
  footer {
    
      display: flex;
      flex-direction: column;
      align-items: center; 
  }

  .footer-left,
  .footer-middle,
  .footer-right {
      width: 100%; 
      text-align: center; 
      margin-bottom: 10px; 
  }
}